import React, { Component } from 'react'
import './sidebar.css'

class sidebar extends Component {
    constructor(props){
        super(props)
        this.state = {
            isShow: false
        }
        this.handleScroll = this.handleScroll.bind(this);
    }

    componentDidMount() {
        this.handleScroll();
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll() {
        if (window.pageYOffset > 800) {
            if (!this.state.isShow) {
                this.setState({isShow: true});
            }
        } else {
            if (this.state.isShow) {
                this.setState({isShow: false});
            }
        }
    }

    render() {
        return ( 
                <ul className="sidebar" style={{opacity: this.state.isShow ? 1 : 0}}>
                    <li className="purple">
                        <a href="https://tifen.xkw.com/" target="_blank" rel="noopener noreferrer">教师端体验</a>
                    </li>
                    <li className="yellow">
                        <span>学生端下载</span>
                        <div className="qrcode">
                            <img src={require("../img/download-qrcode.png")} alt="学生端下载"/>
                            <p>扫描二维码</p>
                            <p>学生端下载</p>
                        </div>
                    </li>
                    <li className="blue">
                        <span>申请<br/>试用</span>
                        <div className="qrcode">
                            <img src={require("../img/apply-qrcode.png")} alt="申请试用"/>
                            <p>扫描二维码</p>
                            <p>申请试用</p>
                        </div>
                    </li>
                </ul>
        );
    }
}

export default sidebar;