import React, { Component, Fragment } from 'react'
import OverPack from 'rc-scroll-anim/lib/ScrollOverPack'
import QueueAnim from 'rc-queue-anim'
import './scene.css'

const sceneList = [
    {
		title: '师生互动',
		content1: '课前预习，课上小测，课后作业',
		content2: '任务轻松收发，成绩实时反馈',
		image: require('../img/scene1.jpg'),
    },
    {
		title: '个性化学习',
		content1: '智能课堂，重练错题，智能补弱',
		content2: '个性化学习，提分易',
		image: require('../img/scene2.jpg'),
    },
    {
		title: '教学管理',
		content1: '各级学情，一手掌握',
		content2: '学习情况随时查，教学管理不用愁',
		image: require('../img/scene3.jpg'),
	}
];

const effectList = [
    {
		title: '试点一：化学垫底班完美逆袭',
		image: require('../img/effect1.png'),
    },
    {
		title: '试点二：一本上线人数短期大幅增多',
		image: require('../img/effect2.png'),
    }
];

class scene extends Component {
    render() {
        return (
			<Fragment>
				<div className="scene">
					<div className="wrapper">
						<h2 className="title" data-text="使用场景">使用场景</h2>
						<OverPack playScale={0.3}>	
							<QueueAnim
								className="scene-list"
								key="scene-list"
								type="bottom"
								leaveReverse
								component="ul"
							>
								{
									sceneList.map((item,index) => {
										const { title, content1, content2, image } = item;
										return (
											<li key={index}>
												<div className="scene-item">
													<img src={image} alt={title} />
													<h3>{title}</h3>
													<p>{content1}</p>
													<p>{content2}</p>
												</div>
											</li>
										)
									})
								}
							</QueueAnim>
						</OverPack>
					</div>
				</div>
				<div className="effect">
					<div className="circle-green"></div>
					<div className="wrapper">
						<h2 className="title" data-text="提分效果">提分效果</h2>
						<OverPack playScale={0.3}>
							<QueueAnim
								className="effect-list"
								key="effect-list"
								type="bottom"
								leaveReverse
								component="ul"
							>
								{
									effectList.map((item,index) => {
										const { title, image } = item;
										return (
											<li key={index}>
												<div className="effect-item">
													<h3>{title}</h3>
													<div className="effect-img">
														<img src={image} alt={title} />
													</div>
												</div>
											</li>	
										)
									})
								}
							</QueueAnim>
						</OverPack>
					</div>
				</div>
			</Fragment>		
        );
    }
}

export default scene;