import React, { Component } from 'react'
import OverPack from 'rc-scroll-anim/lib/ScrollOverPack'
import QueueAnim from 'rc-queue-anim'
import TweenOne from 'rc-tween-one'
import './feature.css'

const { TweenOneGroup } = TweenOne

const features = [
    {
        title: '名师教研课程',
        content: '够权威',
        src: 'https://gw.alipayobjects.com/zos/rmsportal/hBbIHzUsSbSxrhoRFYzi.svg',
        color: '#F5222D',
        shadowColor: 'rgba(245,34,45,.12)',
    },
    {
        title: '板书式解题视频',
        content: '简单易学',
        src: 'https://gw.alipayobjects.com/zos/rmsportal/RpJIQitGbSCHwLMimybX.svg',
        color: '#1890FF',
        shadowColor: 'rgba(24,144,255,.12)',
    },
    {
        title: '全题关联知识图谱',
        content: '快速查缺',
        src: 'https://gw.alipayobjects.com/zos/rmsportal/pbmKMSFpLurLALLNliUQ.svg',
        color: '#FA8C16',
        shadowColor: 'rgba(250,140,22,.12)',
    },
    {
        title: '智能推题',
        content: '精准补弱',
        src: 'https://gw.alipayobjects.com/zos/rmsportal/JsixxWSViARJnQbAAPkI.svg',
        color: '#722ED1',
        shadowColor: 'rgba(114,46,209,.12)',
    },
    {
        title: '数据驱动',
        content: '精准教学',
        src: 'https://gw.alipayobjects.com/zos/rmsportal/aLQyKyUyssIUhHTZqCIb.svg',
        color: '#EB2F96',
        shadowColor: 'rgba(235,45,150,.12)',
    },
    {
        title: '支持虚拟班级',
        content: '新高考不用愁',
        src: 'https://gw.alipayobjects.com/zos/rmsportal/smwQOoxCjXVbNAKMqvWk.svg',        
        color: '#2F54EB',
        shadowColor: 'rgba(47,84,235,.12)',
    }
]

const pointPos = [
    { x: -40, y: -20 },
    { x: 30, y: -30 },
    { x: -85, y: 35 },
    { x: -65, y: 100 },
    { x: 65, y: 25 },
    { x: 70, y: 70, opacity: 0.2 },
]

class feature extends Component {
    state = {
        hoverNum: null,
    }

    onMouseOver = (i) => {
        this.setState({
            hoverNum: i,
        });
    }

    onMouseOut = () => {
        this.setState({
            hoverNum: null,
        });
    }

    getEnter = (e) => {
        const i = e.index;
        const r = (Math.random() * 2) - 1;
        const y = (Math.random() * 10) + 5;
        const delay = Math.round(Math.random() * (i * 50));
        return [
            {
                delay, 
                opacity: 0.4, 
                ...pointPos[e.index],
                ease: 'easeOutBack',
                duration: 300,
            },
            {
                y: r > 0 ? `+=${y}` : `-=${y}`,
                duration: (Math.random() * 1000) + 2000,
                yoyo: true,
                repeat: -1,
            }
        ];
    }

    render() {
        const { hoverNum } = this.state;
        let children = [[], []];
        features.forEach((item,i) => {
            const isHover = hoverNum === i;
            const pointChild = [
                'point-0 left', 'point-0 right','point-ring', 'point-1', 'point-2', 'point-3',
            ].map(className => (
                <TweenOne
                    component="i"
                    className={className}
                    key={className}
                    style={{
                        background: item.color,
                        borderColor: item.color,
                    }}
                />
            ));
            const child = (
                <li key={i.toString()} >
                  <div
                    className="feature-box"
                    onMouseEnter={() => { this.onMouseOver(i); }}
                    onMouseLeave={this.onMouseOut}
                  >
                    <TweenOneGroup
                      className="feature-point-wrapper"
                      enter={this.getEnter}
                      leave={{
                        x: 0, y: 30, opacity: 0, duration: 300, ease: 'easeInBack',
                      }}
                      exclusive={true}
                    >
                      { isHover && pointChild }
                    </TweenOneGroup>
                    <div
                      className="feature-image"
                      style={{boxShadow: `${isHover ? '0 12px 24px' : '0 6px 12px'} ${item.shadowColor}`}}
                    >
                      <img src={item.src} alt="img" />
                    </div>
                    <p>{item.title}</p>
                    <p>{item.content}</p>
                  </div>
                </li>
              );
              children[Math.floor(i / 3)].push(child);
            });

            children = children.map((item, i) => (
                <QueueAnim
                  className="feature-list"
                  key={i.toString()}
                  type="bottom"
                  leaveReverse
                  delay={[i * 100, (children.length - 1 - i) * 100]}
                  component="ul"
                >
                  {item}
                </QueueAnim>
            ));

        return (
            <div className="feature">
                <div className="circle-red"></div>
                <div className="wrapper">
                    <h2 className="title" data-text="产品特色">产品特色</h2>
                    <OverPack playScale={0.3}>
                        {children}
                    </OverPack>
                </div>
            </div>
        );
    }
}

export default feature;