import React, { Component } from 'react'
import OverPack from 'rc-scroll-anim/lib/ScrollOverPack'
import QueueAnim from 'rc-queue-anim'
import './intro.css'

const introList = [
    {
		title: '内容',
		content1: '全学科 全考点',
		content2: '全微课视频 体系化课程',
		image: require('../img/icon1.jpg'),
    },
    {
		title: '工具',
		content1: '师生共用',
		content2: '精准教学工具 高效提分工具',
		image: require('../img/icon2.jpg'),
    },
    {
		title: '方法',
		content1: '教，学，练，测，评',
		content2: '高效备课 个性补弱 快速提分',
		image: require('../img/icon3.jpg'),
	}
];

class intro extends Component {

    render() {
        return (
            <div className="intro">
                <div className="banner">
                    <div className="wrapper clearfix">
                        <a href="https://tifen.xkw.com/" className="logo">
                        </a>
                        <img src={require("../img/headline.png")} alt="学科网提分宝" className="headline" />
                    </div>
                </div>
                <div className="wrapper">
                    <div className="product">
                        <div className="teacher">
                            <div className="tooltip">
                                教师端
                            </div>
                        </div>
                        <div className="student">
                            <div className="tooltip">
                                学生端
                            </div>
                        </div>
                    </div>
                    
                    <OverPack playScale={0.4}>
                        <QueueAnim
                            className="intro-list"
                            key="intro-list"
                            type="bottom"
                            leaveReverse
                            component="ul"
                        >
                            {
                                introList.map((item,index) => {
                                    const { title, content1, content2, image } = item;
                                    return (
                                        <li key={index}>
                                            <div className="intro-item">
                                                <img src={image} alt={title} />
                                                <h3>{title}</h3>
                                                <p>{content1}</p>
                                                <p>{content2}</p>
                                            </div>
                                        </li>    
                                    )
                                })
                            }
                        </QueueAnim>
                    </OverPack>
                </div>
            </div>
        );
    }
}

export default intro;